/* Default styles */

@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@500&displacy=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  /* font-family: cursive; */
  font-family: "Manrope", sans-serif;
  -webkit-user-select: none; /* Safari */        
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* IE10+/Edge */
  user-select: none; /* Standard */
}

body {
  background: #707E7F;
  width: 100vw;
  height: 100vh;
}

h1 {
  width: 100vw;
  text-align: center;
}


/* Timer boxes */

.multiTimerContainer {
  width: 100vw;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  padding: 2vmin;
}

.timerBox {
  border: solid black;
  border-radius: 10%;
  width: 20vw;
  min-width: 250px;
  max-width: 350px;
  aspect-ratio: 1 / 1;

  padding: 10px;
  margin: 2px;
  background-color: #96A4A5;
  position: relative;
}

.timerContentBox {
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  height: 100%;
}

.timerProgressBox {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  border-radius: 10%;
  pointer-events: none;
  border: 0.6em solid red  /*rgb(245, 179, 94)*/;

  /* temp for visualization */
  /* Gbackground-color: rebeccapurple;
  Gopacity: 0.4; */

  /* Ganimation: fill linear reverse forwards; */
  /* Ganimation-duration: 3s; */
  /* Ganimation-play-state: paused; */
}

.timerOverBox {
  position: absolute;
  width: 101%;
  height: 101%;
  left: 0;
  top: 0;
  border-radius: 10%;
  opacity: 0.4;
  background-color: red;

  animation: blink 0.5s linear infinite;
}


/* Time text */

.timeTextBox {
  text-align: center;
  /* font-size: 30px; */
  font-size: 40px;
  width: 100%;
  font-variant-numeric: tabular-nums;
  border-radius: 15px;
  outline: none;

  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}

.smallUnit {
  /* font-size: 15px;
  margin-top: 15px; */
  font-size: 20px;
  margin-top: 20px;
}

.typingIndicator {
  color: white;
  margin-left: -3px;
  margin-right: -2px;

  animation: typingIndicatorAnimation 0.4s alternate infinite;
}


/* Buttons */

.buttonBox {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
}

input[type=button] {
  padding: 3px;
  /* border-radius: 5px; */
  border-radius: 360px; /* to have consistent border radius */
  border: none;
  outline: none;
  width: 46%;
  aspect-ratio: 2.8 / 1;
  color: white;
  font-size: 14px;
  margin-top: 3%;
  margin-bottom: 3%;
}

input[type=button]:hover, input[type=button]:focus {
  color: white;
  font-weight: bolder;
  font-size: 15px;
  filter: brightness(0.8);
}

input[type=button]:hover {
  outline: solid blueviolet;
}

input[type=button]:active {
  outline: dashed black;
}


/* Animations */

@keyframes typingIndicatorAnimation {
  from {
    color: white;
  }
  to {
    color: #B3B5B4;
  }
}

/* animation: https://stackoverflow.com/questions/70233709/html-css-circular-progress-bar-with-image-inside */
@keyframes fill {
  0% {
    clip-path: polygon(50% -20.71%, 50% 50%, 50% 0%, 50% 0%, 50% 0%, 50% 0%, 50% 0%);
    border: 0.6em solid red;
  }
  12.5% {
    clip-path: polygon(50% -20.71%, 50% 50%, 100% 0%, 100% 0%, 100% 0%, 100% 0%, 100% 0%);
  }
  25% {
    clip-path: polygon(50% -20.71%, 50% 50%, 120.71% 50%, 120.71% 50%, 120.71% 50%, 120.71% 50%, 100% 0%);
    border: 0.6em solid orange;
  }
  37.5% {
    clip-path: polygon(50% -20.71%, 50% 50%, 100% 100%, 100% 100%, 100% 100%, 100% 100%, 100% 0%);
  }
  50% {
    clip-path: polygon(50% -20.71%, 50% 50%, 50% 120.71%, 50% 120.71%, 50% 120.71%, 100% 100%, 100% 0%);
    border: 0.6em solid yellow;
  }
  62.5% {
    clip-path: polygon(50% -20.71%, 50% 50%, 0% 100%, 0% 100%, 0% 100%, 100% 100%, 100% 0%);
  }
  75% {
    clip-path: polygon(50% -20.71%, 50% 50%, -20.71% 50%, -20.71% 50%, 0% 100%, 100% 100%, 100% 0%);
    border: 0.6em solid lightgreen;
  }
  87.5% {
    clip-path: polygon(50% -20.71%, 50% 50%, 0% 0%, 0% 0%, 0% 100%, 100% 100%, 100% 0%);
  }
  100% {
    clip-path: polygon(50% -20.71%, 50% 50%, 50% -20.71%, 0% 0%, 0% 100%, 100% 100%, 100% 0%);
    border: 0.6em solid green;
  }
}

@keyframes blink {
  50% {
    opacity: 0;
  }
  /* from {
    background: red;
  }
  to {
    background: none;
  } */
}